@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
/* colors */
.post {
  display: flex;
  padding: 10em 2em 6em 6em; }
  @media (max-width: 599px) {
    .post {
      padding: 4em 1em; } }
  .post__info {
    margin: 0;
    color: #444; }
  .post__title {
    width: 80%;
    line-height: 1.5; }
  .post__content h1 {
    display: none;
    font-size: 3em;
    font-weight: 400;
    line-height: 1.5; }
  .post__content h1 > p {
    line-height: 1.5;
    font-style: italic; }
  .post__content figure {
    margin-left: 0; }
  .post__content figure > img {
    width: 100%; }
  .post__content p {
    line-height: 1.7;
    font-size: 1.2em;
    word-break: break-word; }
  .post__social {
    display: flex;
    justify-content: center;
    padding: 1em 0.5em; }
    .post__social__icon {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-self: center;
      height: 50px;
      width: 50px;
      border-radius: 50%;
      margin: 0 0.5em; }
      .post__social__icon img {
        height: 100%;
        width: 100%; }
      .post__social__icon:hover {
        background-color: #1292b4; }
  .post__line {
    display: block;
    padding: 0.5em 0;
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1); }

